@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background: #E7473C;
  /* box-shadow: 0px 4.42184px 40.23px #F0F0F0; */
}

.formContainer {
  /* flex: 1; */
  position: relative;

  .phoneSvg {
    stroke: #E7473C;
    position: fixed;
    margin: auto;
  }
}

section {
  scroll-snap-align: center;
  overflow: hidden;
}

.worksection {
  scroll-snap-align: center;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}

.portfolio {
  position: relative;

  .progress {
    position: sticky;
    top: 0;
    left: 0;
    padding-top: 50px;
    text-align: center;
    font-size: 36px;

    .progressBar {
      height: 10px;
      background-color: #E7473C;
    }
  }

  .container {

    .wrapper {
      max-width: 1366px;
      height: 100%;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 30px;

      @media (max-width: 738px) {
        flex-direction: column;
      }

      .imageContainer {
        flex: 1;
        height: 50%;

        @media (max-width: 738px) {
          width: 100%;
          max-height: 300px;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        @media (max-width: 738px) {
          object-fit: contain;
        }
      }

      
  
      .textContainer {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 30px;

        @media (max-width: 738px) {
          transform: none !important;
          padding: 10px;
          align-items: center;
          text-align: center;
        }

        h2 {
          @media (max-width: 738px) {
            font-size: 36px;
          }
        }

        p {
          color: gray;
          font-size: 20px;

           @media (max-width: 738px) {
            font-size: 16px;
          }
        }
      }
    }
    
  }
}

.swiper-container {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
}

.slidingTextContainer {
  /* position: absolute; */
  font-size: 50vh;
  /* bottom: -120px; */
  white-space: nowrap;
  color: #ffffff09;
  width: 50%;
  font-weight: bold;
}

.gradient,
.active {
  background: #E7473C;
}

.active {
  color: #F0F0F0;
  padding: 1rem;
  border-radius: 100%;
}

.services-list div {
  background: #262626;
  padding: 40px;
  font-size: 13px;
  font-weight: 300;
  border-right: 10px;
  transition: background 0.5s, transform 0.5s;
}

.services-list div a {
  text-decoration: none;
  color: #fff;
  font-size: 12px;
  margin-top: 20px;
  display: inline-block;
}

.services-list div:hover {
  background: #E7473C;
  transform: translateY(-10px);
}

.formContainer {

  .phoneSvg {
    stroke: #E7473C;
    position: absolute;
    margin: auto;

  }
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-[#F0F0F0];
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-[#F0F0F0] font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text font-bold font-primary bg-[#F0F0F0];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
